body {
  background-color: #002b36;
}

.App {
  text-align: center;
  color: white;
}

.leaflet-container {
  height: 100vh;
  width: 100%;
  cursor: crosshair ;
}

#geotype-selector {
  float: left;
}

button.std {
  color: white;
  background-color: #002b36;
  /* background-color: rgba(255, 255, 255, 0); */
  border: 2px solid;
  border-color: rgba(220, 220, 220, 1.0);
  min-height: 2.7em;
  min-width: 6em;
  -webkit-transition-duration: 50ms;
       -o-transition-duration: 50ms;
          transition-duration: 50ms;
}

button.std:hover {
  background-color: #586e75;
  -webkit-transition-duration: 0s;
       -o-transition-duration: 0s;
          transition-duration: 0s;
}

button.clipboard-btn {
  background-color: #fff;
  background-color: rgba(0,0,0,0);
  border: 0px solid;
  padding: 8px;
  position: absolute;
  right: 20px;
}

button.clipboard-btn:hover {
  background-color: #eef;
}

div.remove-btn {
  background: rgba(0, 0, 0, 0);
  color: #777;
  width: 12px;
  height: 12px;
  line-height: 12px;
  display: inline-block;
  /*border: 1px solid;*/
  border-radius: 20px;
  cursor: pointer;
  vertical-align: middle;
  text-align: center;
  padding: 5px;
  margin: 5px;
}

div.remove-btn:hover {
  background: #EEE;
  color: #111;
}

div#feature-list {
  height: 30vh;
  overflow-y: scroll;
}

div#property-editor {
  height: 15vh;
}

div#source-window {
  background-color: #073642;
  height: 45vh;
}

div.feature-row {
  background: #073642; 
  font-size: 8pt;
  height: 33px;
  display: block;
  cursor: pointer;
  padding: 1px;
  -webkit-transition-duration: 150ms;
       -o-transition-duration: 150ms;
          transition-duration: 150ms;
}

div.feature-row:hover {
  background: #839496; 
  -webkit-transition-duration: 0ms; 
       -o-transition-duration: 0ms; 
          transition-duration: 0ms;
}

div.selected-feature-row {
  border: 1px solid;
}

pre.json-string {
  font-family: monospace;
  font-size: 8pt;
  text-align: left;
  float: left;
  width: 100%;
  height: 80%;
  overflow-x: hidden;
  overflow-y: scroll;
}

div.section-header {
  text-align: left;
}

span.header-text {
  font-family: sans-serif;
  color: #cb4b16;
  font-size: 10pt;
  font-weight: 700;
}

input.property-key {
  width: 30%;
  text-align: left;
  float: left;
}

input.property-value {
  width: 60%;
  text-align: left;
  float: right;
}



